import React, { useEffect, useState } from 'react'
import './MintSection.scss';
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../../redux/data/dataActions";

import { toast } from 'react-toastify';




import loading from '../../assets/images/loading.svg'

import Web3EthContract from "web3-eth-contract";

import rightImg from '../../assets/images/mint-img.webp'

import bgImg from  '../../assets/images/bg.webp'


function MintSection() {
  const truncate = (input, len) =>
    input.length > len ? `${input.substring(0, len)}...` : input;


  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [mintAmount, setMintAmount] = useState(1);

  
  const [paused,setPaused] = useState('')
  const [cost,setCost] = useState(0)
  const [maxPerWallet,setMaxPerWallet]=useState(0);
  const[totalSupply,setTotalSupply]=useState(0)
  const[refresh,setRefresh]=useState(0)
  const[ethereum,setEthereum]=useState(null)

  const[balanceExceeded,setBalanceExceeded]=useState('')


  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const claimNFTs = () => {
    if(claimingNft)return;
    let cost2 = cost;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost2 * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    toast.success(`Minting your ${mintAmount > 1 ? 'Half Breeds' : 'Half Breed'}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint( mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        toast.error("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        toast.success(() =>
          <span>
            Successfully minted your Endangered Kingdom NFT! Visit <a href='https://opensea.io/collection/'>opensea.io</a> to view it.
          </span>
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
        setRefresh(refresh+1)
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > maxPerWallet) {
      newMintAmount = maxPerWallet;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();

  }, []);

  useEffect(() => {
    getData();

  }, [blockchain.account]);

  const setAmountInput = (amount) => {
    amount = Number(amount)
    if (isNaN(amount)) return;
    if (amount > maxPerWallet) {
      setMintAmount(maxPerWallet)
    }
    else {
      setMintAmount(amount)
    }
  }

  const mint = () => {
    claimNFTs();
    getData();
  }

  const connectWallet=()=>{
    if(paused===''&&ethereum===null)return;
    dispatch(connect());
    getData();
  }


  const initConnect=async()=>{
    const { ethereum } = window;
   
    Web3EthContract.setProvider(ethereum);
    const address=CONFIG.CONTRACT_ADDRESS
    const abiResponse = await fetch("/config/abi.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const abi = await abiResponse.json();

    const SmartContractObj = new Web3EthContract(
      abi,
      address
    );
    
    console.log(ethereum)

    if(ethereum!==undefined){
      try{
        const cost=await SmartContractObj.methods.cost().call()
        const maxPerPublicAccount=await SmartContractObj.methods.maxMintAmountPerPublicAccount().call()
        const maxPerFreePublicAccount=await SmartContractObj.methods.maxFreeMintAmountPerAccount().call()
    
        const paused=await SmartContractObj.methods.paused().call()
        const totalSupply=await SmartContractObj.methods.totalSupply().call()
        // console.log(cost.call())
        if(totalSupply>=500){
          setMaxPerWallet(maxPerPublicAccount)
          setCost(cost)
        }
        else{
          setMaxPerWallet(maxPerFreePublicAccount)
          if(paused===true){
            setCost(cost)
    
          }else{
            setCost(0)
          }
          
        }
        setTotalSupply(totalSupply)
        
        setPaused(paused)
      }
      catch{
        
      }
    }
    setEthereum(ethereum)
  }

  useEffect(()=>{
    if(CONFIG.CONTRACT_ADDRESS==="")return;
    initConnect()
  },[CONFIG,refresh])
  console.log(data.totalSupply)

  useEffect(()=>{
    if(blockchain.account===null)return;

    blockchain.smartContract.methods.balanceOf(blockchain.account).call().then((result)=>{
      console.log(typeof(result))
      if(Number(result)>0){
        setBalanceExceeded(false);
      }
      else{
        setBalanceExceeded(false);
      }
    })

  },[blockchain.account])

  return (
    <div className='MintSection'>
      <div className='mint-bgHolder'>
        <img src={bgImg}/>
      </div>
     <div className='MintSection-inner'>
         
        <div className='MintSection-left'>
         
          <div className='font-l text-green family-grand-bold'>
            ENDANGERED KINGDOM
          </div>
    
          {blockchain.account !== null &&paused!==''&&
            (
              <span className="font-xs text-light-gray mt-1">
                Price:
                <span className="font-xs text-white bold">
                {` 0.044 Eth per NFT + Gas Fee`}

                </span>
              </span>
            )
          }
         

         {blockchain.account !== null&&paused!==''&&
          <span className="font-xs text-light-gray  mt-1">
            Public Mint:  <span className="font-xs text-white bold">{paused===true||paused===''?'Stealth Launch':'LIVE'}</span> 
          </span>}

          {blockchain.account !== null &&

            <div className="mt-1">
              <a className="font-xs text-white mt-1" href={CONFIG.SCAN_LINK}>
                {truncate(CONFIG.CONTRACT_ADDRESS, 15)}
              </a>
            </div>
          }
          {paused!==''&&
            <span className="desktop-mint-supply font-xl text-white  mt-1 family-bold">
              <span className="text-green "> 0 </span> / 2512
            </span>
          }
          <div className='MintSection-left-bottom'>
            <div className='MintSection-bossNum'>

              <div className='MintSection-bossNum-top'>
                <button onClick={() => { decrementMintAmount() }} className='mint-plus-btn   font-s bold   text-white'>
                  -
                </button>
                <input onChange={(e) => { setAmountInput(e.target.value) }} value={mintAmount} placeholder='1' className='font-s  text-white' />
                <button onClick={() => { incrementMintAmount() }} className='mint-plus-btn  font-s bold  text-white'>
                  +
                </button>
              </div>

              <div className='MintSection-bossNum-bottom font-xs text-white'>

            {blockchain.account === "" || blockchain.smartContract === null ? (
              // <button  onClick={() => {connectWallet()}} className='mint-button'>
              <button   className='mint-button'>
 
                {ethereum===null?
                <img src={loading}></img>
                :paused===true?
                  <span className='font-s bold'>
                    MINT (Coming Soon)
                  </span>
                :
                  <span className='font-s family-bold'>
                    CONNECT WALLET
                  </span>
                }
              </button>
              ) : (
                <div className={balanceExceeded?'mt-2':''}>
                  {balanceExceeded===false?<button onClick={() => { mint() }}  className='mint-button'>
                    <span  className='font-s bold'>
                      {claimingNft?'MINTING':'MINT'}
                    </span>
                    {claimingNft&& <img style={{marginLeft:'15px'}} src={loading}></img>}
                  </button>:balanceExceeded===true?<span className='font-m  text-red extra-bold '>
                    Mint Limit Exceeded
                  </span>:<button  className='mint-button'>
                    <img  src={loading}></img>
                  </button>}
                </div>
             
            )}

              </div>

            </div>
            
          </div>
        </div>
        <div className='MintSection-right'>
          <img src={rightImg}/>
        </div>
      </div>
    </div>
  )
}

export default MintSection
