import './App.scss';
import { useEffect, useState } from 'react';
import smoothscroll from "smoothscroll-polyfill";

import { toast, ToastContainer } from 'react-toastify';

import "react-toastify/dist/ReactToastify.css";
import Header from './components/header/Header';
import Roadmap from './components/roadmap/Roadmap';
import About from './components/about-section/About'
import TeamSection from './components/team-section/TeamSection'
import Footer from './components/footer/Footer'
import Carousel from './components/carousel/Carousel';
import MintSection from './components/mint-section/MintSection'
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Input from '@mui/material/Input';
import Button from '@mui/material/Button';

import FaqSection from './components/faq-section/FaqSection'

const style = {
  position: 'absolute',
  display:"flex",
  flexDirection:'column',
  justifyContent:'space-evenly',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 300,
  height:200,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};
window.__forceSmoothScrollPolyfill__ = true;

function App(){
  const [open, setOpen] = useState(false);
  const [appDisplayed, setAppDisplayed] = useState(true);
  const handleOpen = () => setOpen(false);
  const handleClose = () => setOpen(false);
  const pass='the2testlinkforendangeredkingdom2022'
  const name='the2saint'
  useEffect(() => {
    smoothscroll.polyfill();
    handleOpen()
  }, []);

  const submitAuth=(e)=>{
    e.preventDefault()
    let subimttedname=e.target[0].value
    let subimttedpassword=e.target[1].value
    if(subimttedname===name&&subimttedpassword===pass){
      handleClose()
      setAppDisplayed(true)
    }
    else{
      toast.error('Invalid details.')
    }
  }

  return (
    <div className="App">
      <ToastContainer  position="top-right" autoClose={5000} closeOnClick />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          
          <form onSubmit={(e)=>submitAuth(e)} style={{height:'100%',width:'100%',display:'flex',flexDirection:'column',justifyContent:'space-evenly'}}>
            <center>
              <Typography id="modal-modal-title" className='family-grand-bold' variant="h6" component="h2">
                Endangered Kingdom
              </Typography>
            </center>
            <Input required={true} placeholder='name'>
            </Input>
            <Input type='password' required={true} placeholder='password' >
            </Input>
            <Button type='submit'>Submit</Button>
          </form>
        </Box>
      </Modal>
      {appDisplayed===true&&<div className="App-inner">
        <Header/>
        <MintSection/>
          <About/>
        <Roadmap/>
        <div className='mt-4 section text-center'>
          <TeamSection/>
        </div>      
        <Carousel/>
        <FaqSection/>
        <Footer/>
     
      </div>}

    </div>
  );
}

export default App;
