import React from 'react';
import './FaqSection.scss'
import Faq from './Faq'


function FaqSection() {
 
  return (
    <div className='faq-section'>
        <div className='font-xl  text-green family-bold'>
            FAQ
        </div>
        <div className='faq-section-inner'>
            <Faq
                question='What network is it on?'
                answer="On the Ethereum network."
            />
            <Faq
                question=' Which wallets are supported?'
                answer="All wallets that support ERC-721 tokens, although we recommend using MetaMask."
            />
            <Faq
                question="What is the price of the NFT?"
                answer="The pre-sale price is 0.08 ETH, after the
                pre-sale, the price goes up to 0.1 ETH."
            />
            <Faq
                question='How many can I buy?'
                answer='Whitelisted individuals are allowed to purchase a
                maximum of five NFTs during the pre-sale, during the public sale this limit will
                be increased to 10 NFTs, afterwards there will be no cap for the remaining
                NFTs.
                '
            />
            <Faq
                question='When and where can I buy?'
                answer='The pre-sale starts May 1, once the pre-sale
                ends, the public sale will start. All NFTs can be purchased on OpenSea and
                minted on our website.
                '
            />
       

        </div>
    </div>
  );
}

export default FaqSection;
