import React, { useState } from 'react'
import './Header.scss'
import twitterLogo from '../../assets/images/twitter-logo.png'
import discordLogo from '../../assets/images/discord-logo.png'
import instaLogo from '../../assets/images/insta-logo.png'

import closeIcn from '../../assets/images/close.png'
import menuIcn from '../../assets/images/menu.png'

import logo from '../../assets/images/logo.png'

function Header() {


    const[mobileNavOpen,setMobilenavOpen]=useState(false)
    const navigate=(link)=>{
        window.open(link)

    }
    const navigateTo=(location)=>{

        var height = document.getElementsByClassName("Header")[0].offsetHeight + 15;
        var container = document.getElementsByClassName("App")[0];
        var offsetTop;

        if(window.innerWidth<750){
            openNavbar()
        }

        switch (location) {
            case "STORY":
              offsetTop =document.getElementById("story").offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "MINT":
              offsetTop =document.getElementsByClassName("MintSection")[0].offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "ABOUT":
              offsetTop =document.getElementsByClassName("About")[0].offsetTop- height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "ROADMAP":
              offsetTop =document.getElementById("roadmap").offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "TEAM":
              offsetTop =document.getElementsByClassName("TeamSection")[0].offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "FAQ":
              offsetTop =document.getElementsByClassName("faq-section")[0].offsetTop - height;
              container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "LEGENDARY":
                offsetTop =document.getElementsByClassName("Carousel")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            default:
              break;
        }
    
    }
    

    const openNavbar=()=>{
        const nav=document.getElementsByClassName('Header-mobile-nav')[0];

        if(!mobileNavOpen){
            nav.style.display='flex';
            setTimeout(()=>{
                nav.style.height='100vh';
                setMobilenavOpen(true)
            },[100])
        }
        else{
            nav.style.height='0vh';
            setTimeout(()=>{
                nav.style.display='none';
                setMobilenavOpen(false)
            },[500])
        }
        
    }

    return (
        <div className='Header'>
       
            <div className='Header-mobile-nav'>
                <div className='Header-mobile-nav-inner'>
                    <img  src={logo}/>
                    <span onClick={()=>navigateTo('MINT')}  className='font-m text-white family-bold'>
                        MINT
                    </span>
                    <span onClick={()=>navigateTo('ABOUT')}  className='font-m text-white family-bold'>
                        ABOUT
                    </span>
                    <span onClick={()=>navigateTo('ROADMAP')}  className='font-m text-white family-bold'>
                        ROADMAP
                    </span>
                    <span onClick={()=>navigateTo('TEAM')}  className='font-m text-white family-bold'>
                        TEAM
                    </span>
                    <span onClick={()=>navigateTo('LEGENDARY')}  className='font-m text-white family-bold'>
                        LEGENDARY
                    </span>
                    <span onClick={()=>navigateTo('FAQ')}  className='font-m text-white family-bold'>
                        FAQ
                    </span>
                </div>
                
            </div>
            <div className='Header-inner'>
                <div className='Header-left'>
                    <img className='Header-logo' src={logo}/>
                    <div className='Header-left-nav'>
                       
                        <span onClick={()=>navigateTo('MINT')}  className='font-s  family-bold'>
                            MINT
                        </span>
                        <span onClick={()=>navigateTo('ABOUT')}  className='font-s  family-bold'>
                            ABOUT
                        </span>
                        <span onClick={()=>navigateTo('ROADMAP')}  className='font-s  family-bold'>
                            ROADMAP
                        </span>
                        <span onClick={()=>navigateTo('TEAM')}  className='font-s  family-bold'>
                            TEAM
                        </span>
                        <span onClick={()=>navigateTo('LEGENDARY')}  className='font-s  family-bold'>
                            LEGENDARY
                        </span>
                        <span onClick={()=>navigateTo('FAQ')}  className='font-s  family-bold'>
                            FAQ
                        </span>
                    </div>
                    
                </div>
  
                <div className='Header-right'>
               
                
                    <button  onClick={()=>navigate('https://discord.gg/YeQRAUxEmF')} className='social-btn'>
    
                        <img src={discordLogo} alt='logo'/>
                    </button>
                    <button onClick={()=>navigate('https://www.instagram.com/endangeredkingdomnft/')} className='social-btn insta-btn'>                        
                        <img src={instaLogo} alt='logo'/>
                    </button>
                </div>
                <button onClick={openNavbar} className='header-menu-btn' >
                    {mobileNavOpen?
                    
                    <img src={closeIcn} alt='logo'/>:
                    <img src={menuIcn} alt='logo'/>}
                </button>
            </div>
            
        </div>
    )
}

export default Header
